<template>
  <div class="page-container">
    <Loader v-if="loader" />
    <div class="wrap-1500">
      <div class="wrap-1300">
        <div class="step-box mb-5">
          <ul class="reset">
            <li class="current">
              <i></i>
              <p>購物清單</p>
            </li>
            <li class="current">
              <i></i>
              <p>填寫資料</p>
            </li>
            <li>
              <i></i>
              <p>訂單完成</p>
            </li>
          </ul>
        </div>
        <p class="summary">請填寫已下表單內容，<span class="star">*</span>為必填欄位。</p>
        <Form v-slot="{ errors }" @submit="createOrder" style="max-width: 900px; margin: auto;">
          <div class="row">
            <div class="col-md-6">
              <div class="mb-3">
                <label for="name" class="form-label"><span class="star">*</span>姓名</label>
                <Field id="name" name="姓名" type="text" class="form-control"
                  :class="{ 'is-invalid': errors['姓名'] }"
                  placeholder="請輸入姓名" rules="required"
                  v-model="form.user.name"></Field>
                <ErrorMessage name="姓名" class="invalid-feedback"></ErrorMessage>
              </div>
            </div>
            <div class="col-md-6">
              <div class="mb-3">
                <label for="tel" class="form-label"><span class="star">*</span>手機</label>
                <Field id="tel" name="手機" type="tel" class="form-control"
                  :class="{ 'is-invalid': errors['手機'] }"
                  placeholder="請輸入手機號碼" :rules="phoneCheck"
                  v-model="form.user.tel"></Field>
                <ErrorMessage name="手機" class="invalid-feedback"></ErrorMessage>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-md-6">
              <div class="mb-3">
                <label for="email" class="form-label"><span class="star">*</span>Email</label>
                <Field id="email" name="email" type="email" class="form-control"
                  :class="{ 'is-invalid': errors['email'] }"
                  placeholder="請輸入 Email" rules="email|required"
                  v-model="form.user.email"></Field>
                <ErrorMessage name="email" class="invalid-feedback"></ErrorMessage>
              </div>
            </div>
            <div class="col-md-6">
              <div class="mb-3">
                <label for="address" class="form-label"><span class="star">*</span>地址</label>
                <Field id="address" name="地址" type="text" class="form-control"
                  :class="{ 'is-invalid': errors['地址'] }"
                  placeholder="請輸入地址" rules="required"
                  v-model="form.user.address"></Field>
                <ErrorMessage name="地址" class="invalid-feedback"></ErrorMessage>
              </div>
            </div>
          </div>  

          <div class="mb-3">
            <label for="message" class="form-label">留言</label>
            <textarea name="" id="message" class="form-control" cols="30" rows="10"
              v-model="form.message"></textarea>
          </div>

          <div class="btn-wrap d-flex">
            <router-link to="/cartFlow" class="btn-style" title="返回修改"><span>返回修改</span><i class="bi bi-pencil-square"></i></router-link>
            <button class="btn-style" title="送出訂單"><span>送出訂單</span><i class="bi bi-arrow-right"></i></button>
          </div>
        </Form>
      </div>
    </div>
  </div>
</template>

<script>
  import Loader from '@/components/Loader.vue';
  export default {
    components:{
      Loader,
    },
    data(){
      return{
        loader: false,
        form: {
          user: {
            name: '',
            email: '',
            tel: '',
            address: '',
          },
          message: '',
        },
      }
    },
    methods: {
      phoneCheck(value) {
        const phoneNumber = /^(09)[0-9]{8}$/;
        return phoneNumber.test(value) ? true : "需要正確的電話號碼";
      },
      createOrder() {
        const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/order`;
        const order = this.form;
        this.$http.post(url, { data: order })
        .then((res) => {
          this.$router.push({
            path: "cartFinish",
            query: {
              id: res.data.orderId,
            },
          });
        });
      },
    },
    mounted(){
      this.loader = true;
      setTimeout(() => {
        this.loader = false;
      }, 500);
    }
  }
</script>

<style lang="scss" scoped>
.summary{
  font-size: 18px;
  margin-bottom: 25px;
  text-align: center;
}
.star{ color: #ff8400; }
.btn-wrap{
  max-width: 900px;
  justify-content: flex-end;
  margin: 30px auto 0;
  a{
    margin-right: 10px;
    &:last-child{ margin-right: 0; }
  }
}
.form-control:focus{ border: 1px solid #4f200d; }
@media (max-width: 767px) {
  .btn-wrap button{
    color: #000;
  }
}
</style>